@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

}


.main-container{
    padding-top: 1.5rem;
    top:0px;
    left: 0px;
    width: 100%;
}
.navbar-main-container{
    display: flex;
    height: 100%;
    align-items: center;
    
}
.main-logo a{
    color: black;
    margin-left: 3.5rem;
    text-decoration: none;
    font-size: 30px;
    font-family: 'Playfair Display', serif;
}
.spacer{
    flex: 1 1;
}
.navbar-items{
    display: flex;
    
}
.navbar-items a{
    text-decoration: none;
    color: black;
}
.navbar-items ul{
    list-style: none;
    margin: 0px;
    padding: 0px;
    padding-right: 0.5rem;
    display: flex
}
.navbar-items li{
    color: black;
    text-transform: uppercase;
    padding-right: 2.5rem;
}
.hamburger{
    height: 1.5rem;
    padding-right: 5rem;
}
.phone-nav{
    position: absolute;
    height: 100px;
    background-color: seagreen;
    width:70px;
}

.phone-nav ul{
    list-style: none;
    margin: 0px;
    padding: 0px;
    padding-right: 0.5rem;
}
@media only screen and (max-width: 600px) {
    .navbar-items ul{
        list-style: none;
        margin: 0px;
        padding: 0px;
        padding-right: 0.5rem;
        display: none;
    }
    .hamburger{
        margin-left: 40%;
    }


}
.carousel-caption{
    top: 5rem;
    text-align: left !important;
    text-align: justify;
    right: 0% !important;
    left: 3rem !important;
    width: 40% !important;
}
.carousel-caption h3{
    left: 0 !important;
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 2rem;
}
.carousel-caption p{
    font-size: 16px;
}
.carousel-caption button{
    color:red;
    background-color: transparent;
    border-color: rgb(105, 38, 38);
    padding: 15px 20px;
}
@media only screen and (max-width: 600px) {
    .carousel-caption{
        top:0.5rem;
    
        text-align: left !important;
        text-align: justify;
        right: 0% !important;
        left: 1rem !important;
        width: 60% !important;
    }
    .carousel-caption h3{
        top:0;
        left: 0;
        font-size: 15px;
        font-weight: lighter;
        margin-bottom: 1rem;
    }
    .carousel-caption p{
        font-size: 10px;
        width: 100%;
    }
    .carousel-caption button{
        color:red;
        background-color: transparent;
        border-color: rgb(105, 38, 38);
        padding: 5px 5px;
    }
    
  }
.col{
    height: 20rem;
}
