    display: block;
     position: absolute;
    height: 5rem;
    top: 40%;
    left: 0;
    background-color: white;
    width: 60%;

} */

.image-info{
   
    display: none;
    position: absolute;
   height: 5rem;
   top: 40%;
   left: 0;
   background-color: white;
   width: 60%;
   transition: 0.5s ease-in;
}
.container-fluid{
    margin-top: 3rem;
}


   
.title{
    font-weight: 500;
    padding: 6px;
    font-size: 22px;
}
.keywords{
    font-style: italic;
    padding-left: 8px;
    font-weight: 600;

}
.col:hover .image-info{
    
    display: block;
}

.container h2{
    text-align: center;
    font-size: 5rem;
    font-weight: lighter;
}
.wrapper{
    margin-top: 2rem;
}
.container span{
    font-family: 'Playfair Display', serif;

}
.image-container{
    background-color: rgb(202, 165, 165);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 160px;
    width: 160px;
    border-radius: 50%;
    margin: 0 auto;
    border: none;
   
    
}

.col h4{
    text-align: center;
}

.col p{
    text-align: center;
    
}

.info-wrapper{
    text-align: center;
    color: white;
     height: 31.25rem;
}

.info-wrapper h2{
    font-weight: lighter;
    font-size: 4.5rem;
}


@media only screen and (max-width: 600px) {
    .info-wrapper h2{
        font-size: 1.5rem;
        
    }
    
  }
.info-wrapper span{
    font-family: 'Playfair Display', serif;
    font-weight: bold;
    
}
.contact-info{
    padding: 0.8rem 3rem 0rem 3rem;
}
.contact-us{
    border: 1px solid;
    margin-bottom: .2rem;
    padding: 0.7rem 1.5rem 0.7rem 1.5rem;
    background-color: #3a8f86;
    color: white;
    text-decoration: none;
    list-style: none;
}
.contact-us:hover{
    background-color: rgb(223, 189, 185);
    list-style: none;
    text-decoration: none;
}
.footer{
    position: absolute;
    top: 30%;
   left: 20%;
    
}
.footer li{
    text-decoration: none;
    list-style: none;
}
